import React from "react";
import styles from './navigation.module.css'

import NavigationButton from './navigation-button'
import textTitle from './text-title'

const MENU = [
    {
        key: 'make-an-nft',
        icon: '',
        title: 'Make an NFT'
    },
    {
        key: 'business-signup',
        icon: '',
        title: 'Business Signup'
    },
    {
        key: 'Subscribe-for-upcoming-drops',
        icon: '',
        title: 'Subscribe for Upcoming Drops'
    },
    {
        key: 'nft-standards',
        icon: '',
        title: 'NFT Standards'
    },
    {
        key: 'download-nft-creator',
        icon: '',
        title: 'Download NFT Creator'
    }
]

function Navigation(){
    return (
    <nav className={styles.navigator}>
        <div className={styles.nav}>
            <textTitle bold>Ownable-NS</textTitle>
            <div className={styles.navBtn}>
            { 
                MENU.map((menu) => {
                    const showElement = menu.icon.length > 0
                    return (
                        <NavigationButton
                        key= {menu.key}
                        isIcon= {showElement}
                        >
                        <textTitle className={styles.Title} bold>{menu.title}</textTitle>
                        </NavigationButton>
                    )
                })
            }
            </div>
        </div>
    </nav>
    )
}

export default Navigation;